import { PieChart, Pie, Cell, Tooltip } from "recharts";
import { ProcessedTimeSummary } from "../model/Report";

/*
const data01 = [
  { name: "開発", value: 400 },
  { name: "テスト", value: 300 },
  { name: "MTG", value: 300 },
  { name: "休憩", value: 200 },
];
*/

type PieChartViewProps = {
  summaryList: ProcessedTimeSummary[];
};

const COLORS = [
  "#0088FE",
  "#00C49F",
  "#FFBB28",
  "#FF8042",
  "#FF6347",
  "#9932CC",
];

const PieChartView = ({ summaryList }: PieChartViewProps) => {
  const renderCustomizedLabel = ({
    cx,
    cy,
    midAngle,
    innerRadius,
    outerRadius,
    index,
    percent,
  }: any) => {
    const RADIAN = Math.PI / 180;
    const radius = 25 + innerRadius + (outerRadius - innerRadius);
    const x = cx + radius * Math.cos(-midAngle * RADIAN);
    const y = cy + radius * Math.sin(-midAngle * RADIAN);

    return (
      <text
        x={x}
        y={y}
        fill={COLORS[index % COLORS.length]}
        textAnchor={x > cx ? "start" : "end"}
        dominantBaseline="central"
        fontSize={12}
      >
        {summaryList[index].name} ({(percent * 100).toFixed(0) + "%"})
      </text>
    );
  };

  return (
    <PieChart width={350} height={350}>
      <Pie
        dataKey="value"
        isAnimationActive={false}
        data={summaryList}
        cx="50%"
        cy="50%"
        outerRadius={80}
        fill="#8884d8"
        label={renderCustomizedLabel}
      >
        {summaryList.map((entry, index) => (
          <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
        ))}
      </Pie>
      <Tooltip />
    </PieChart>
  );
};

export default PieChartView;
