import db, { ICategory, IKeyword } from "../infra/LocalDB";
import { parseMulti } from "../lib/NoteParser";
import PieChartView from "./PieChartView";
import { ProcessedTimeSummary } from "../model/Report";

type ReportViewWeeklyProps = {
  weeklyValues: string[];
  keywords: IKeyword[];
  categories: ICategory[];
};

const ReportViewWeekly = ({
  weeklyValues,
  keywords,
  categories,
}: ReportViewWeeklyProps) => {
  if (!keywords) {
    return <div>NO REPORT</div>;
  }

  const weeklyResults = parseMulti(weeklyValues, keywords);
  const weeklyTotalProcessedTime = weeklyResults.reduce(
    (acc, cur) => acc + cur.processedTime,
    0
  );

  function getCategoryName(categoryId: number) {
    const name = categories?.find((elem) => elem.id === categoryId)?.name;
    return name ?? "その他";
  }

  const summaryList: ProcessedTimeSummary[] = weeklyResults
    .sort((a, b) => a.categoryId - b.categoryId)
    .map((it) => {
      return {
        name: getCategoryName(it.categoryId),
        value: it.processedTime / 60,
      };
    });

  return (
    <div>
      <p>【WEEKLY】</p>
      {summaryList.map((it) => (
        <div>
          {it.name}[{it.value.toFixed(1)}h]
        </div>
      ))}
      <p>Total {(weeklyTotalProcessedTime / 60).toFixed(1)}h</p>
      <PieChartView summaryList={summaryList} />
    </div>
  );
};

export default ReportViewWeekly;
