import { useState, useEffect, useCallback } from "react";
import { useLiveQuery } from "dexie-react-hooks";
import { useParams, Link } from "react-router-dom";
import MyHeader from "./Header";
import {
  getTodayYmd,
  getNextYmd,
  getPrevYmd,
  getTimestampOfDay,
  getTodayDayOfWeekJP,
  getWeeklyTimestampRange,
} from "../lib/Utils";
import db from "../infra/LocalDB";
import AutoSaveTextArea from "./AutoSaveTextArea";
import ReportView from "./ReportView";
import PieChartView from "./PieChartView";
import ReportViewWeekly from "./ReportViewWeekly";

const DailyReport = () => {
  const { dateParam } = useParams();
  const currentYmd: string = dateParam ? dateParam : getTodayYmd();
  const nextYmd = getNextYmd(currentYmd);
  const prevYmd = getPrevYmd(currentYmd);

  const keywords = useLiveQuery(() => db.keywords.toArray(), []);
  const categories = useLiveQuery(() => db.categories.toArray(), []);

  // 記録日のタイムスタンプ(その日の始まり)
  const targetTimestamp = getTimestampOfDay(currentYmd);
  const weeklyTimestampRange = getWeeklyTimestampRange(currentYmd);
  console.log(`ymd: ${currentYmd}, unix: ${targetTimestamp}`);
  /*
  console.log(
    `start: ${weeklyTimestampRange[0]}, end: ${weeklyTimestampRange[1]}`
  );*/
  const [note, setNote] = useState("");

  // 週次の統計表示用
  const weeklyNotes = useLiveQuery(
    () =>
      db.reports
        .where("date_timestamp")
        .between(weeklyTimestampRange[0], weeklyTimestampRange[1])
        .toArray(),
    [note]
  );

  // DBに内容を保存
  const handleSave = useCallback(() => {
    console.log("Save");
    db.reports.put({ date_timestamp: targetTimestamp, note: note });
  }, [targetTimestamp, note]);

  useEffect(() => {
    // Ctrl+sのイベントを上書き
    const handleKeyDown = (event: KeyboardEvent) => {
      if (
        (event.ctrlKey && event.key === "s") ||
        (event.metaKey && event.key === "s")
      ) {
        event.preventDefault(); // デフォルトの保存動作をキャンセルする
        handleSave();
      }
    };

    document.addEventListener("keydown", handleKeyDown);
    return () => {
      document.removeEventListener("keydown", handleKeyDown);
    };
  });

  useEffect(() => {
    // 非同期でDBから取得した初期値をstateに入れるための処理
    const fetchDataAndSetData = async () => {
      const report = await db.reports
        .where({ date_timestamp: targetTimestamp })
        .first();
      if (report) {
        setNote(report.note);
      }
    };
    fetchDataAndSetData();
  }, [targetTimestamp]);

  function resetContents() {
    setNote("");
  }

  // 中央寄せするコンテナのCSS
  const centeringContainer = {
    display: "flex",
    justifyContent: "center",
  };

  return (
    <div>
      <div style={centeringContainer}>
        <MyHeader />
      </div>

      <div style={centeringContainer}>
        <h1>
          {currentYmd}({getTodayDayOfWeekJP(currentYmd)})
        </h1>
        <p>
          <Link onClick={resetContents} to={`/daily/${prevYmd}`}>
            Prev
          </Link>{" "}
          |{" "}
          <Link onClick={resetContents} to={`/daily/${nextYmd}`}>
            Next
          </Link>
        </p>
      </div>

      <div style={centeringContainer}>
        <AutoSaveTextArea
          name="report"
          cols={40}
          rows={20}
          placeholder="今日やったこと"
          value={note}
          onChange={setNote}
          onSave={handleSave}
          delay={2000}
        />
        <ReportView
          value={note}
          keywords={keywords ?? []}
          categories={categories ?? []}
        />
        <ReportViewWeekly
          weeklyValues={weeklyNotes?.map((e) => e.note) ?? []}
          keywords={keywords ?? []}
          categories={categories ?? []}
        />
      </div>
    </div>
  );
};

export default DailyReport;
